    font-size: 10px;
    text-align: center;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
}

.instructions b {
    font-weight: normal;
    color: #777777;
}

@media screen and (max-width: 600px) {
    .container {
        padding: 5%;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
    }

    .container .sun {
        -ms-flex-positive: 1;
            flex-grow: 1;
        margin: 20%;
        margin-top: 15%;
    }
}



